<template>
  <center>
  {{symbol}}
  </center>
</template>


<script>
import Vue from 'vue';
import Amplify, { Auth, Analytics } from 'aws-amplify';

export default {
  name: 'Symbol',
  data() {
    return {
      symbol: '',
      news: []
    }
  },


  created() {
    Analytics.record({ name: 'webUsmSymbol' });
    this.symbol = this.$route.params.symbol;
  },

  methods: {
    percent: function(floatnum) {
      return (parseFloat(floatnum)*100.0).toFixed(2) + "%";
    }
  }
}
</script>



